import uniSwipeAction from "@/components/uni-swipe-action/uni-swipe-action.vue";
export default {
	data() {
		return {
			list:[],
			options: [{
				text: '编辑',
				style: {
					backgroundColor: '#dd524d'
				}
			}]
		};
	},
	onLoad(options) {
		//绑定分类列表
		let loginRes = this.checkLogin('../cate/cate', 2);
		if (!loginRes) {
			return false;
		}
		
		uni.request({
			url: this.apiBasic + '/cate',
			method: 'GET',
			success: res => {
				console.log(res);
				this.list = res.data;
			},
			fail: () => {},
			complete: () => {}
		});
		
	},
	components: {
		uniSwipeAction
	},
	methods: {
		editName(item) {
			uni.redirectTo({
				url: '/pages/cate_edit/cate_edit?_id=' + item._id+'&name='+item.name
			});
		}
	}
};
